.price {
    padding: 96px 0 105px 0;
    background-color: #00B582;

    @media screen and (max-width:767px) {
        padding: 77px 0 44px 0;
    }
}

.price__title {
    max-width: 1040px;
    margin-bottom: 40px;

    font-size: 80px;
    line-height: 0.88;
    text-transform: uppercase;
    color: var(--black-text-color);

    span {
        color: var(--white-text-color);
    }

    @media screen and (max-width:767px) {
        font-size: 50px;
        margin-bottom: 30px;
    }
}

.price__border {
    border-top: 3px dashed rgba(0, 0, 0, 0.5);
    margin-bottom: 77px;

    @media screen and (max-width:767px) {
        margin-bottom: 59px;
    }
}

.price__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;

    @media screen and (max-width:767px) {
        gap: 7.5px;
    }
}

.price__card {
    width: calc((100% - 20px) / 3);
    box-sizing: border-box;
    padding: 23px 37px 37px 37px;
    background-color: #FFFFFF;
    border-radius: 20px;
    border: 1px solid #000000;
    color: var(--black-text-color);

    display: flex;
    flex-direction: column;

    transition-duration: 750ms;
    cursor: pointer;

    &:hover {
        background-color: #000000;
        color: var(--white-text-color);

        .card__estimated {
            background-color: #FFFFFF;
            color: var(--black-text-color);
        }

        .card__accept {
            border-color: #FFFFFF;
        }
    }

    @media screen and (max-width:1123px) {
        width: 100%;
    }

    @media screen and (max-width:767px) {
        padding: 17px 27px 27px 27px;
    }
}

.card__sharp {
    font-family: 'Sulphur Point';
    font-weight: 700;
    font-size: 40px;
    line-height: 1;
    letter-spacing: -0.04em;
    margin-bottom: 5px;

    @media screen and (max-width:767px) {
        font-size: 30px;
        margin-bottom: 2.5px;
    }
}

.card__title {
    font-family: 'Sulphur Point';
    font-weight: 700;
    font-size: 60px;
    line-height: 0.86;
    letter-spacing: -0.04em;
    text-transform: uppercase;
    margin-bottom: 40px;

    @media screen and (max-width:767px) {
        font-size: 42px;
        margin-bottom: 29.5px;
    }
}

.card__price {
    font-family: 'Plus Jakarta Display';
    font-weight: 600;
    font-size: 53px;
    line-height: 0.97;
    text-align: center;
    letter-spacing: -0.04em;
    text-transform: uppercase;
    margin-bottom: 43px;

    @media screen and (max-width:767px) {
        font-size: 38px;
        margin-bottom: 31px;
    }
}

.card__accept {
    display: inline-block;
    padding: 27px 36px;
    text-align: center;
    font-family: 'Plus Jakarta Display';
    font-weight: 500;
    line-height: 20px;
    border: 1px solid #000000;
    border-radius: 60px;

    margin: 0 auto 35px auto;

    transition-property: border-color;
    transition-duration: 750ms;

    @media screen and (max-width:767px) {
        font-size: 12px;
        margin-bottom: 25.5px;
        padding: 21px 25.5px;
    }
}

.card__weeks {
    font-family: 'Plus Jakarta Display';
    font-weight: 600;
    font-size: 53px;
    line-height: 0.97;
    text-align: center;
    letter-spacing: -0.04em;
    text-transform: uppercase;
    margin-bottom: 50px;

    @media screen and (max-width:767px) {
        font-size: 38px;
        margin-bottom: 36px;
    }
}

.card__estimated {
    font-size: 17px;
    line-height: 1.4;
    text-align: center;
    text-transform: uppercase;

    padding: 35px 0;
    background-color: #00B582;
    border-radius: 120px;

    transition-duration: 750ms;

    @media screen and (max-width:767px) {
        padding: 26px 0;
        font-size: 14px;
    }
}

.price__pagination {
    margin: 28px auto 0 auto;

    position: relative;
    display: block;
    width: 33px;
    height: 9px;
    background-color: #FFFFFF;
    border-radius: 19px;

    &::after {
        content: '';
        width: 9px;
        height: 9px;
        position: absolute;
        top: 0;
        left: -15px;
        background-color: #FFFFFF6E;
        border-radius: 50%;
    }

    &::before {
        content: '';
        width: 9px;
        height: 9px;
        position: absolute;
        top: 0;
        right: -15px;
        background-color: #FFFFFF6E;
        border-radius: 50%;
    }
}